<style>
  * {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    overflow: hidden;
  }

  body {
    font: 13px "Noto Sans";
    background:black;
  }

  .main {
    display: flex;
    flex-direction: column;
    justify-content: top;
    align-items: center;
    height: 100vh;
  }

  form {
    background: #01b3ed;
    padding: 3px;
    width: 100%;
		display: flex;
  }

  form input {
    border: 0;
    padding: 10px;
    width: 90%;
    margin-right: 0.5%;
    background: #b34e7e;
    color: #fff8b8;
		font-size: 14px;
	}

	@media screen and (device-aspect-ratio: 375/667) {
		form input {
			font-size: 16px;
		}
	}

  form button {
    background: #fff8b8;
    border: none;
    padding: 10px 15px;
    color: #01b3ed;
  }

	#chatWindow {
		height: 350px;
    width: 100%;
    border: 3px solid #01b3ed;
		overflow: auto;
	}

	@media (min-height: 845px) {
		#chatWindow {
			height: 600px;
		}
	}

  #messages {
		align-self: center;
    list-style-type: none;
    margin: 0;
    padding: 0;
    font-size: 14px;
  }

  #messages li {
    padding: 5px 10px;
		border-radius: 1em;
		margin: 0.5em auto;
		width: 95%;
    background: #fff8b8;
    color: #01b3ed;
		overflow-wrap: break-word;
  }

  #messages li:nth-child(odd) {
    background: #01b3ed;
    color: #fff8b8;
  }

  ::placeholder {
    color: #fff8b8;
  }

	#numUsers {
		color: #fff8b8;
		padding: 1em;
		font-size: 14px;
	}

  .graph {
    background-color: black;;
    height:300px;
    width:100%;
  }
</style>

<svelte:head>
  <title>Chat App</title>

	<meta name="viewport" content="width=device-width, initial-scale=1.0">
</svelte:head>

<svelte:window on:unload={emitUserDisconnect}/>

<script>

  import { onMount } from 'svelte';
 	import * as vis from 'vis-network';
  import io from "socket.io-client";
	import { fade } from "svelte/transition";
	
  import Heading from "../components/Heading.svelte";
  
  var data = {
    nodes: new vis.DataSet([
      {id:0, label:'Hello'}]
      ),
    edges: new vis.DataSet()
  };

  let options = {
    physics:{
    enabled: true,
    }, 
   autoResize: true,
            height: '100%',
            width: '100%',
    interaction: { multiselect: true },
    nodes:{
      font: {color: '#ffffff'},
      shapeProperties: {
              useBorderWithImage:false,
              interpolation:true
      },
      //color:'#0077C8' 
      color: '#33333333'
    }, edges: {
      color: "0077C8"
      ,smooth: { enabled: true}
    }
  };

  let network;

  onMount(async () => {
    // create a network
    var container = document.getElementById('mynet');
    network = new vis.Network(container, data, {});    
    network.on("select", function (params) {
      // nodeSelection.selectNode(params.nodes[0]);
      //  selection = params;
    });


    setTimeout(() => {
         // network.fit();
        }, 1000);
  
  });
  
	const socket = io();
	const placeholder = "Type your message here...";
	const greeting = `You have joined the chat. Use '/nick your_nickname' to set your nickname!`
  let messages = [greeting];
	let message = "";
	let name = 'Anonymous';
  let numUsersConnected = 0;
  
  let nodeMe={};

  socket.on("message", function(message) {	
    if (message) { 	
      messages = messages.concat(message);
      updateScroll();
    }
	
  });
  
  socket.on("logged", function(message) {		
   // messages = messages.concat(message);
    
    if (message.isMe) 
    {
      // replace default node with received node from server
      message.id = 0 
    };

    data.nodes.update(message);


    if (!message.isMe) {
      data.edges.add({from: message.id, to:0});
    }

	});
	
	socket.on("user joined", function({message, numUsers}) {
		messages = messages.concat(message);
    numUsersConnected = numUsers;
  //  data.nodes.add({label: message})
		updateScroll();
	});

	socket.on("user left", function(numUsers) {
		numUsersConnected = numUsers;
		updateScroll();
  });
 
  socket.on("userGraph", function(g) {

    data.nodes.update(g.nodes);
    data.edges.update(g.edges);
  });

	function emitUserDisconnect() {
		socket.emit('user disconnect', name); 
	}

  function handleSubmit() {
		message = message.trim();
		
		if (message == '') {
			return;
		}

		let messageString = `${name}: ${message}`;

		if (message.slice(0, 5) == '/nick') {
			let newName = message.slice(6);
      messageString = `Server: ${name} changed their nickname to ${newName}`;
    
      name = newName;
        socket.emit("login", name);
      
		}

    messages = messages.concat(messageString);
    
		socket.emit("message", messageString);

		updateScroll();

		message = "";
	}
	
	function updateScroll() {
		const chatWindow = document.getElementById('chatWindow');
		setTimeout(() => {
			chatWindow.scrollTop = chatWindow.scrollHeight;			
		}, 0);
  }
  
</script>

<body>
  <div class="graph" id="mynet"></div>
  <div class="main">
   
    <div id="chatWindow">
      <ul id="messages">
        {#each messages as message}
          <li transition:fade>{message}</li>
        {/each}
      </ul>
    </div>
    <form action="">
      <input id="m" autocomplete="off" {placeholder} bind:value={message} />
      <button on:click|preventDefault={handleSubmit}>Send</button>
    </form>
		<p id="numUsers">There {numUsersConnected == 1 ? 'is' : 'are'} {numUsersConnected} {numUsersConnected == 1 ? 'user' : 'users'} currently chatting!</p>
  </div>
</body>
